/**
 * 2
 * @param x
 * @returns {*}
 */
  let fmtResult = x =>  {
    var f = parseFloat(x);
    if (isNaN(f)) {
      return false;
    }
    var f = Math.round(x*100)/100;
    var s = f.toString();
    var rs = s.indexOf('.');
    if (rs < 0) {
      rs = s.length;
      s += '.';
    }
    while (s.length <= rs + 2) {
      s += '0';
    }
    return s;


  }
/**
 * 4
 * @param x
 * @returns {*}
 */
  let fmtFloat = x =>  {
    var f = parseFloat(x);
    if (isNaN(f)) {
      return false;
    }
    var f = Math.round(x*10000)/10000;
    var s = f.toString();
    var rs = s.indexOf('.');
    if (rs < 0) {
      rs = s.length;
      s += '.';
    }
    while (s.length <= rs + 4) {
      s += '0';
    }
    return s;


  }

  let sex =val =>{
    return val.toString()==='1'?'':'';
  }

//   {} 
export  {moneyFormat,fmtResult,fmtFloat,sex}

